import { css } from "styled-components";

export interface Margins {
	mt?: string
	mb?: string
	ml?: string
	mr?: string
	/** margin shorthand notation
	 * @example margin: 25px 50px 75px 100px;
	 * @link https://www.w3schools.com/css/css_margin.asp
	 */
	m?: string
}

export const marginsCss = css<Margins>`
	margin-top: ${({ mt }) => mt};
	margin-bottom: ${({ mb }) => mb};
	margin-left: ${({ ml }) => ml};
	margin-right: ${({ mr }) => mr};
	margin: ${({ m }) => m};
`;
